import { g, syyType, shapeType, ssfl_sgd, ccssm_sgd } from './common'
/**
 * Chapter1 溢洪道水力学计算
 * 部分计算参考以下网址
 * https://raymondmcguire.github.io/?e=HYCAL&&theme=Image-Visual-Lab
 */
export module Chapter1 {

  /**
   * 1.1泄流能力计算
   * 1.1.1开敞式幂曲线实用堰
   * @param n 闸孔数目
   * @param b 单孔宽度
   * @param P_1 上游堰高
   * @param H_d 定型设计水头,m,对于高堰, H_d = (75% ~ 95)H_max ,H_max为校核流量对应的水头
   * @param H 堰上水头,m,取堰前 0 0 3H ~ 6H 处；
   * @param v_0 引渠的行进流速,m/s；
   * @param C 上游坡角修正系数
   * @param m 流量系数
   * @param epsilon 侧收缩系数
   * @param sigma_m 淹没系数
   * @returns Q PMF下泄量
   */
  export function kcsmqxsyy(n: number, b: number, P_1: number,
    H_d: number, H: number, v_0: number,
    C: number, m: number, epsilon: number,
    sigma_m: number) {
    let B = n * b
    let H_0 = H
    if (P_1 < 1.33 * H_d) {
      H_0 = H + v_0 * v_0 / (2 * g)
    }
    // console.log("H0:"+H_0);
    let Q = C * m * epsilon * sigma_m * B * Math.sqrt(2 * g) * Math.pow(H_0, 1.5)
    return Q
  }

  /**
   * 1.1泄流能力计算
   * 1.1.2宽顶堰
   * @param v_0 堰前 0 0 3H ~ 6H 行进流速
   * @param H 堰上水头
   * @param epsilon 侧收缩系数
   * @param n 孔数
   * @param b 单孔宽度
   * @param P_1 上游堰高
   * @param b_edge 堰进口底坎边缘:方角true,圆角false
   * @returns Q 泄流能力
   */
  export function kdy(v_0: number, H: number,
    epsilon: number, n: number, b: number,
    P_1: number, b_edge: boolean) {
    let H_0 = H + v_0 * v_0 / (2 * g)
    let P1divH0 = P_1 / H_0

    // console.log("H0:"+H_0);
    // console.log("P1/H0:"+P1divH0);

    // 流量系数
    let m = 0
    if (b_edge) {
      if (P1divH0 <= 3) {
        m = 0.32 + 0.01 * (3 - P1divH0) / (0.46 + 0.75 * P1divH0)
      } else {
        m = 0.32
      }
    } else {
      if (P1divH0 <= 3) {
        m = 0.36 + 0.01 * (3 - P1divH0) / (1.5 * P1divH0)
      } else {
        m = 0.36
      }
    }
    // console.log("m:"+m);
    let Q = m * epsilon * n * b * Math.sqrt(2 * g) * Math.pow(H_0, 1.5)

    let outObj = { 'm': m, 'Q': Q }
    return outObj
  }

  /**
   * 1.1泄流能力计算
   * 1.1.3驼峰堰
   * @param P_1 上游堰高
   * @param v_0
   * @param H
   * @param epsilon 侧收缩系数
   * @param n 孔数
   * @param b 单孔宽度
   * @param b_type a型true,b型false
   * @returns Q
   */
  export function tfy(P_1: number, v_0: number, H: number,
    epsilon: number, n: number, b: number,
    b_type: boolean) {
    let H_0 = H + v_0 * v_0 / (2 * g)
    let P1divH0 = P_1 / H_0

    // console.log("H0:"+H_0);
    // console.log("P1/H0:"+P1divH0);

    let m = 0
    if (b_type) {
      if (P1divH0 <= 0.24) {
        m = 0.385 + 0.171 * Math.pow(P1divH0, -0.657)
      } else {
        m = 0.414 * Math.pow(P1divH0, -0.0652)
      }
    } else {
      if (P1divH0 <= 0.34) {
        m = 0.385 + 0.224 * Math.pow(P1divH0, 0.934)
      } else {
        m = 0.452 * Math.pow(P1divH0, -0.032)
      }
    }

    // console.log("m:"+m);
    let Q = m * epsilon * n * b * Math.sqrt(2 * g) * Math.pow(H_0, 1.5)
    let outObj = { 'm': m, 'Q': Q }
    return outObj
  }

  /**
   * 1.1.4带胸墙孔口泄流能力
   * 1.1.4.1带胸墙实用堰
   * @param H
   * @param v_0 堰前 0 0 3H ~ 6H 处断面的平均流速,m/s；
   * @param b 单孔宽度,m；
   * @param n 孔数目；
   * @param P_1
   * @param H_d 定型设计水头,m, 可按堰顶以上最大水头Hmax的56%~77%；
   * @param D 孔口高度,m；
   * @returns Q
   * */
  export function dxqsyy(H: number, v_0: number,
    b: number, n: number, P_1: number, H_d: number,
    D: number) {
    let H_0 = H + v_0 * v_0 / (2 * g)
    let P1divHd = P_1 / H_d
    let mu = 0
    let HdivD = H / D
    if (P1divHd > 0.6) {
      mu = 0.7 + 0.1 * (HdivD - 2)
    } else {
      mu = 0.6 + 0.1 * (HdivD - 1.5)
    }

    let Q = mu * n * b * D * Math.sqrt(2 * g * H_0)
    return Q
  }

  /**
   * 1.1.4带胸墙孔口泄流能力
   * 1.1.4.2带胸墙平底闸泄流计算
   * @param H 堰上水头
   * @param v_0 堰前 0 0 3H ~ 6H 处断面的平均流速,m/s；
   * @param b 单孔宽度,m；
   * @param n 孔数目；
   * @param mu 流量系数,圆滑孔口的流量系数在设计时可取0.90
   * @param D 孔口高度,m；
   * @returns Q
   * */
  export function dxqpdzxljs(H: number, v_0: number,
    b: number, n: number, mu: number,
    D: number) {
    let H_0 = H + v_0 * v_0 / (2 * g)

    let Q = mu * n * b * D * Math.sqrt(2 * g * (H_0 - D))
    return Q
  }

  /**
   * 1.2泄槽水面线计算
   * @param type 实用堰类型
   * @param Q_c 单个泄槽流量，m^3/s
   * @param b_c 单个泄槽宽，m
   * @param n_z 糙率
   * @param i 泄槽底坡
   * @param phi 流速系数，一般0.95 左右
   * @param H_0 计算起始断面渠底以上总水头
   * @param N 断面数
   * @param L 泄槽长
   * @param alpha 掺气水深系数，取1～1.4s/m
   */
  export function slotSurfaceLine(type: syyType, Q_c: number, b_c: number, n_z: number, i: number, phi: number, H_0: number, N: number, L: number, alpha: number) {
    let h_k = Math.pow((Q_c * Q_c) / (b_c * b_c * g), 1 / 3)
    let R_k = b_c * h_k / (b_c + 2 * h_k)
    let C_k = 1 / n_z * Math.pow(R_k, 1 / 6)
    let i_k = ((Q_c * Q_c) / (b_c * b_c)) / (h_k * h_k * C_k * C_k * R_k)
    // let outStr = "L断面水深：";
    let outArray = new Array()

    let h0 = 0
    let learning_rate = 0.01
    let theta = Math.atan(i)
    let precision = 0.001
    if (i > i_k) {
      // 急流
      if (type == syyType.syy) {
        // 实用堰
        // SGD

        // identify h0
        for (let t = 0; t < 10000; t++) {
          let h0_right = (Q_c / b_c / (phi * Math.sqrt(2 * g * (H_0 - h0 * Math.cos(theta)))))
          h0 -= learning_rate * (h0 - h0_right)

          let chkValue = Math.abs(h0 - (Q_c / b_c / (phi * Math.sqrt(2 * g * (H_0 - h0 * Math.cos(theta))))))
          if (chkValue < precision) {
            // success
            break
          }
        }
      } else if (type == syyType.kdy) {
        // 宽顶堰
        h0 = h_k
      }

      // identify h0 - hn
      let delta_L = L / (N - 1)
      let outH = h0
      // console.log(outH);
      // outStr += outH.toFixed(3) + "|";
      outArray.push(outH.toFixed(3))
      for (let nn = 0; nn < N - 1; nn++) {
        outH = ssfl_sgd(shapeType.RECTANGLE, 0, learning_rate, outH, b_c, Q_c, n_z, theta, i, alpha, delta_L, precision)
        // outStr += outH.toFixed(3) + "|";
        outArray.push(outH.toFixed(3))
        // console.log(outH);
      }
    } else {
      // 输出缓流
      // outStr += "缓流";
      // console.log('缓流')
    }

    // return outStr;
    return outArray
  }
  /**
   * 1.3水流空化数计算程序框图
   * @param nabla
   * @param H
   * @param h_v
   * @param v
   * @returns S
   *  */
  export function slkqhs(nabla: number, H: number, h_v: number, v: number) {
    let h_a = 1.33 - nabla / 900
    let sigma = (H + h_a - h_v) / (v * v / (2 * g))
    let out = '不需要设掺气坎'
    if (sigma < 0.3) {
      out = '需要设掺气坎'
    }

    let outObj = { 'sigma': sigma, 'out': out }
    // console.log(sigma);
    return outObj
  }

  /**
   * 1.4挑流消能挑距和冲坑计算
   * 1.4.1 挑距
   * @param H_ksw
   * @param H_bk
   * @param v_0
   * @param h
   * @param theta
   * @param h_2
   * @param t
   * @returns L
   */
  export function tj(H_ksw: number, H_bk: number, v_0: number,
    h: number, theta: number, h_2: number, t: number) {
    theta = Math.PI * theta / 180
    let H_0 = (H_ksw - H_bk) + v_0 * v_0 / 19.6
    let h_1 = h / Math.cos(theta)
    // console.log("h1="+h_1);
    // console.log("H_0="+(H_ksw - H_bk));
    let v_1 = 0.95 * Math.sqrt(2 * g * (H_0 - h_1))
    // console.log("v_1="+v_1);

    let L = (Math.sin(theta) + Math.sqrt(Math.sin(theta) * Math.sin(theta) + 2 * g * (h_1 + h_2) / (v_1 * v_1))) * Math.cos(theta) * v_1 * v_1 / g

    let tan_beta = Math.sqrt(Math.tan(theta) * Math.tan(theta) + 2 * g * (h_2 + h * Math.cos(theta)) / (v_1 * v_1 * Math.cos(theta) * Math.cos(theta)))
    let L_c = t / tan_beta
    // console.log(L_c);
    let L_total = L + L_c
    return L_total
  }

  /**
   * 1.4挑流消能挑距和冲坑计算
   * 1.4.2 冲坑计算
   * @param H_up
   * @param H_down
   * @param K
   * @param Q
   * @param b
   * @param H_down_deep 下游水深
   * @returns T
  */
  export function ckjs(H_up: number, H_down: number, K: number,
    Q: number, b: number, H_down_deep: number) {
    let H = H_up - H_down
    let t = K * Math.sqrt(Q / b) * Math.pow(H, 0.25)
    let T = t - H_down_deep
    return T
  }

  /**
   * 1.5底流消能计算
   * 1.5.1 水平光滑护坦上的水跃计算
   * @param Q
   * @param b
   * @param H_0
   * @returns
  */
  export function spghhtsdsyjs(Q: number, b: number, H_0: number) {
    // let phi = Math.sqrt(1- 0.1*Math.sqrt(E)/Math.pow((Q/b),1/3));
    // console.log(phi);
    let phi = 0.8

    let h_1 = 10
    let learning_rate = 0.01
    let precision = 0.001
    // identify h0
    for (let t = 0; t < 10000; t++) {
      let h1_right = H_0 - (Q / b) * (Q / b) / (2 * g * phi * phi * h_1 * h_1)
      h_1 -= learning_rate * h1_right

      let chkValue = Math.abs(H_0 - (Q / b) * (Q / b) / (2 * g * phi * phi * h_1 * h_1))
      if (chkValue < precision) {
        // success
        break
      }
    }
    // console.log(h_1);
    let v_1 = Q / (b * h_1)
    let F_r1 = v_1 / Math.sqrt(g * h_1)
    let h_2 = 0.5 * h_1 * (Math.sqrt(1 + 8 * F_r1 * F_r1) - 1)
    // console.log(h_2);
    let L = 0
    if (F_r1 >= 4.5 && F_r1 <= 15.5) {
      // 5.9 ~ 6.15
      L = 5.9 * h_2
    } else {
      L = 9.4 * (F_r1 - 1) * h_1
    }
    return L
  }

  /**
   * 1.5底流消能计算
   * 1.5.2 下挖式消力池水跃计算
   * @param Q
   * @param b
   * @param H_0
   * @param phi
   * @param h_t
   * @param sigma
   * @returns
  */
  export function xwsxlcsyjs(Q: number, b: number, H_0: number, phi: number,
    h_t: number, sigma: number) {
    let h_1 = 10
    let learning_rate = 0.01
    let precision = 0.001
    // identify h0
    for (let t = 0; t < 10000; t++) {
      let h1_right = H_0 - (Q / b) * (Q / b) / (2 * g * phi * phi * h_1 * h_1)
      h_1 -= learning_rate * h1_right

      let chkValue = Math.abs(H_0 - (Q / b) * (Q / b) / (2 * g * phi * phi * h_1 * h_1))
      if (chkValue < precision) {
        // success
        break
      }
    }

    let v_1 = Q / (b * h_1)
    let F_r1 = v_1 / Math.sqrt(g * h_1)
    let h_2 = 0.5 * h_1 * (Math.sqrt(1 + 8 * F_r1 * F_r1) - 1)
    // console.log(h_2);
    let delta_Z = (Q * Q / (2 * g * b * b)) * ((1 / (phi * phi * h_t * h_t)) - (1 / (sigma * sigma * h_2 * h_2)))
    // console.log(delta_Z);
    let s = sigma * h_2 - h_t - delta_Z
    // console.log(s);
    let L = 5.9 * h_2
    let L_k = 0.8 * L
    let outObj = { 'L_k': L_k, 's': s }
    return outObj
  }

  /**
   * 1.6 侧槽溢洪道水力学计算
   * 1.6.1 侧槽断溢流前缘的总长度计算
   * @param Q 溢洪道最大泄流流量，m3/s
   * @param m 流量系数，根据堰型选用；可采用实用堰、宽顶堰河梯形堰等，实际工程以实用堰居多
   * @param H 堰上水头，m，取堰前3H_0 ~ 6H_0处
   * @param v_0 引渠的行进流速，m/s
   * @param eplison_k 侧槽溢流堰侧收缩系数，无闸实用堰，两边侧墙圆角到直角取0.7~1.0
   *
   */
  export function cydylqydzcdjs(Q: number, m: number, H: number, v_0: number, eplison_k: number) {
    let H_0 = H + v_0 * v_0 / (2 * g)
    let L = Q / (eplison_k * m * Math.sqrt(2 * g) * Math.pow(H_0, 3 / 2))
    return L
  }

  /**
   * 1.6 侧槽溢洪道水力学计算
   * 1.6.2 侧槽断水面线计算
   * @param Q 溢洪道最大泄流流量，m3/s
   * @param m 流量系数，根据堰型选用；可采用实用堰、宽顶堰河梯形堰等，实际工程以实用堰居多
   * @param H 堰上水头，m，取堰前3H_0 ~ 6H_0处
   * @param v_0 引渠的行进流速，m/s
   * @param eplison_k 侧槽溢流堰侧收缩系数，无闸实用堰，两边侧墙圆角到直角取0.7~1.0
   * @param b_s
   * @param b_m
   * @param N
   * @param n_z
   * @param i
   * @returns outArray
   */
  export function ccdsmxjs(Q: number, m: number, H: number, v_0: number, eplison_k: number,
    b_s: number, b_m: number, N: number, n_z: number, i: number) {
    let L = cydylqydzcdjs(Q, m, H, v_0, eplison_k)
    let hk = Math.pow(Q * Q / (b_m * b_m * g), 1 / 3)
    let hm = hk * (1.2 - 0.3 * ((b_s / b_m) - 1))
    // let outStr = "侧槽内水面线水深：";
    let outArray = new Array()
    let n = 1
    let bn = b_s + (b_m - b_s) / L * (L - (n - 1) * L / (N - 1))
    let vn = Q / (bn * hm)
    let Rn = bn * hm / (bn + 2 * hm)

    let Qn = Q
    let delta_H = 0
    let delta = i * L / (N - 1)

    // outStr += hm.toFixed(4) + "|";
    outArray.push(hm.toFixed(4))
    for (let ii = 2; ii < N + 1; ii++) {
      let res = ccssm_sgd(n_z, b_s, b_m, Q, L, N, ii, delta, hm, vn, Rn, Qn, delta_H)
      hm = res[0]
      if (hm != -1) {
        // outStr += hm.toFixed(4) + "|";
        outArray.push(hm.toFixed(4))
        delta_H = res[1]
        vn = res[2]
        Rn = res[3]
        Qn = res[4]
      }
    }

    return outArray
  }
}
